.footer {
  background: linear-gradient(90deg, #1b263b, #0d1b2a);
  color: #f0f0f0;
  text-align: center;
  padding: 20px 30px;
  font-size: 0.95rem;
  font-weight: 500;
  box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.1);
}

.footer p {
  margin: 0;
  color: #f0a500;
  letter-spacing: 0.5px;
}
